html {
  font-size: 18px;

  &[data-text-size="medium"] {
    font-size: 20px;
  }

  &[data-text-size="large"] {
    font-size: 24px;
  }

  @media #{$small-and-down} {
    font-size: 16px;
  }
}

body {
  font-family: 'Poppins', sans-serif;

  h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, blockquote {
    font-family: 'Poppins', sans-serif;
  }

  &[data-font="3"] {
    font-size: 1.2rem;
  }
}

h1, .h1 {
  font-size: rem-calc(50px);
  font-weight: 600;
}

h2, .h2 {
  font-size: rem-calc(36px);
  font-weight: 600;
}

h3, .h3 {
  font-size: rem-calc(24px);
  font-weight: 600;
  letter-spacing: 1px;
}

h4, .h4 {
  font-size: rem-calc(24px);
}

h5, .h5 {
  font-size: 1rem;
  text-transform: uppercase;
}

a {
  text-decoration: underline;
  font-weight: bold;
  transition: all 0.3s;

  &:hover, &:active {
    text-decoration-style: double;
    // color: $color-green;

    html[data-contrast="true"] & {
      color: $color-white;
    }
  }
}

.main {
  p, li, dt, dd {
    line-height: 1.6em;
  }

  dt {
    font-weight: bold;
    clear: both;
  }

  dd {
    margin-bottom: 1em;
    margin-left: 2em;

    @media #{$xsmall-and-down} {
      margin-left: 1em;
    }
  }

  ul {
    li {
      list-style: none;
      position: relative;
      padding-left: 30px;

      &::before {
        content: '\2022';
        display: block;
        position: absolute;
        left: 10px;
        speak: none;
        color: $primary-color;

        html[data-contrast="true"] & {
          color: $color-white;
        }
      }
    }
  }
}

blockquote {
  border-left: 0;

  &.wp-block-pullquote {
    clear: both;
    padding-left: 3em;
    padding-right: 3em;
  }
}

.label {
  // font-family: $font-serif;
  font-weight: 600;
}

strong {
  font-weight: 700;
}

abbr[title] {
  border-bottom: 0;
}



